.container {
  background-color: var(--color-CodGray);
}

.header__content {
  position: relative;
}

.header__container {
  padding: 0;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: var(--color-LimedOak);
}

.header__content {
  color: var(--color-LimedOak);
}

.header__city {
  margin-top: 13px;
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 21px;
}

.header__text {
  margin-top: 16px;
  margin-bottom: 47px;
  font-size: 14px;
  line-height: 20px;
}

.header__title {
  margin: 0;
  font-family: "Yeseva One", "Georgia", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 42px;
  color: var(--color-white);
}

.header__link {
  padding: 13px 17px;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  color: var(--color-white);
  border: 0.5px solid var(--color-LimedOak);
  box-sizing: border-box;
}

.header__img {
  margin-top: 26px;
  position: relative;
  bottom: -6px;
  right: -23px;
}

.logo {
  padding-top: 14px;
  padding-bottom: 18px;
}

.logo svg {
  width: 50px;
  height: 50px;
  fill: #A07B50;
}

.social__header {
  display: none;
}
